import React, { useState } from 'react';

function ExpenseForm({ onAddExpense, income, setIncome }) {
    const [category, setCategory] = useState('');
    const [amount, setAmount] = useState('');

    const handleSubmit = (e) => {
        e.preventDefault();
        if (category && amount) {
            const newExpense = { category, amount: parseFloat(amount) };
            onAddExpense(newExpense);
            setCategory('');
            setAmount('');
        }
    };

    const handleReset = () => {
        setCategory('');
        setAmount('');
    };

    return (
        <div style={{ maxWidth: '600px', margin: '0 auto' }}>
            <div style={{ padding: '20px', backgroundColor: '#f8f9fa', borderRadius: '8px', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                <h2 style={{ marginBottom: '20px', textAlign: 'center' }}>Enter Expenses</h2>
                <form onSubmit={handleSubmit} style={{ width: '100%' }}>
                    <div style={{ display: 'flex', justifyContent: 'center', marginBottom: '15px', width: '100%' }}>
                        <label style={{ marginRight: '10px', width: '120px' }}>Category</label>
                        <select
                            value={category}
                            onChange={(e) => setCategory(e.target.value)}
                            style={{ flex: '1', padding: '5px', maxWidth: '200px' }}
                        >
                            <option value="">Select Category</option>
                            <option value="Food">Food</option>
                            <option value="Transportation">Transportation</option>
                            <option value="Housing">Housing</option>
                            <option value="Entertainment">Entertainment</option>
                        </select>
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'center', marginBottom: '15px', width: '100%' }}>
                        <label style={{ marginRight: '10px', width: '120px' }}>Amount</label>
                        <input
                            type="number"
                            value={amount}
                            onChange={(e) => setAmount(e.target.value)}
                            style={{ flex: '1', padding: '5px', maxWidth: '200px' }}
                            placeholder="Enter amount"
                        />
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'center', marginBottom: '15px', width: '100%' }}>
                        <label style={{ marginRight: '10px', width: '120px' }}>Income</label>
                        <input
                            type="number"
                            value={income}
                            onChange={(e) => setIncome(e.target.value)}
                            style={{ flex: '1', padding: '5px', maxWidth: '200px' }}
                            placeholder="Enter income"
                        />
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'center', width: '100%' }}>
                        <button type="submit" style={{ marginRight: '10px', padding: '8px 16px', backgroundColor: '#007bff', color: '#fff', border: 'none', borderRadius: '4px' }}>Enter Expense</button>
                        <button type="button" onClick={handleReset} style={{ padding: '8px 16px', backgroundColor: '#6c757d', color: '#fff', border: 'none', borderRadius: '4px' }}>Reset</button>
                    </div>
                </form>
            </div>
        </div>
    );
}

export default ExpenseForm;
