import React, { useState } from 'react';
import ExpenseForm from './ExpenseForm';
import ExpenseChart from './ExpenseChart';

function App() {
    const [expenses, setExpenses] = useState([]);
    const [income, setIncome] = useState('');

    // Function to handle adding expenses
    const handleAddExpense = (newExpense) => {
        setExpenses([...expenses, newExpense]);
    };

    // Calculate total expenses
    const totalExpenses = expenses.reduce((acc, expense) => acc + parseFloat(expense.amount), 0);

    // Calculate savings
    const savings = income ? income - totalExpenses : 0;

    return (
        <div className="container mt-5">
            <h1 className="text-center mb-4">Expense Tracker</h1>
            <ExpenseForm onAddExpense={handleAddExpense} income={income} setIncome={setIncome} />
            <div className="card mb-4">
                <div className="card-body">
                    <h2 className="card-title">Savings Overview</h2>
                    <p className="card-text">Income: ${income ? income : 'Enter your income'}</p>
                    <p className="card-text">Total Expenses: ${totalExpenses.toFixed(2)}</p>
                    <p className="card-text">Savings: ${savings.toFixed(2)}</p>
                </div>
            </div>
            <ExpenseChart expenses={expenses} />
        </div>
    );
}

export default App;
