// ExpenseChart.js

import React from 'react';
import { PieChart, Pie, Tooltip, Cell } from 'recharts';

function ExpenseChart({ expenses }) {
    // Categorize expenses
    const categorizedExpenses = expenses.reduce((acc, curr) => {
        const categoryIndex = acc.findIndex(item => item.name === curr.category);
        if (categoryIndex !== -1) {
            acc[categoryIndex].value += parseFloat(curr.amount);
        } else {
            acc.push({ name: curr.category, value: parseFloat(curr.amount) });
        }
        return acc;
    }, []);

    // Generate random colors for each category
    const colors = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#AF19FF', '#FF1919', '#00FFFF', '#FF00BF', '#FF7F7F', '#003366'];

    return (
        <div className="chart-container">
            <h2 className="text-center">Expense Breakdown</h2>
            <PieChart width={600} height={400}>
                <Pie
                    dataKey="value"
                    data={categorizedExpenses}
                    cx={300}
                    cy={200}
                    outerRadius={120}
                    fill="#8884d8"
                    label={({ name, percent }) => `${name} (${(percent * 100).toFixed(2)}%)`}
                >
                    {
                        categorizedExpenses.map((entry, index) => (
                            <Cell key={`cell-${index}`} fill={colors[index % colors.length]} />
                        ))
                    }
                </Pie>
                <Tooltip />
            </PieChart>
        </div>
    );
}

export default ExpenseChart;
